import React, { useEffect, useRef, useState } from "react";
import { Card, Form, Modal, Col, Row } from "react-bootstrap";
import { AiFillEye } from "react-icons/ai";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import { BiSearch } from "react-icons/bi";
import { BsCalendarFill, BsDownload } from "react-icons/bs";
import PaymentImage from "../../../assets/images/paymentdownloadimage.png";

import "react-datepicker/dist/react-datepicker.css";
import ReactDatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { useDispatch, useSelector } from "react-redux";
import moment from "moment";
import { getPaymentDetails } from "../../../app/features/paymentDetails/paymentDetails.slice";
import PrintComponents from "react-print-components";
import InvoicePDF from "../../../components/Theme/InvoicePDF";

export default function AdminPaymentDetails() {
  const [selectedDate, setSelectedDate] = useState();
  const [show, setShow] = useState(false);
  const [searchValue, setSearchValue] = useState();
  const [viewInvoice, setViewInvoice] = useState();
  const dispatch = useDispatch();
  const printButtonRef = useRef(null);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const { allPaymentDetails } = useSelector((state) => state?.PaymentDetails);

  const handlePrintClick = () => {
    setShow(false);
    setTimeout(function () {
      printButtonRef?.current?.click();
    }, 1000);
  };

  const viewInvoiceData = (data) => {
    handleShow();
    setViewInvoice(data);
  };

  const columns = [
    {
      dataField: "invoiceNumber",
      text: "Invoice Number",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1", width: "17%" },
    },
    {
      dataField: "patientName",
      text: "Patient Name",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1", width: "17%" },
    },
    {
      dataField: "doctorName",
      text: "Doctor Name",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "amount",
      text: "Amount",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "createdDate",
      text: "Created Date",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (cell) => {
        return moment(cell).format("YYYY-MM-DD");
      },
    },
    {
      dataField: "status",
      text: "Status",
      sort: false,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (row, cell) => (
        <p
          className={`mb-0 text-center py-1 custom-width-class ${
            cell?.status === "Paid" && "active-status"
          }`}
        >
          {cell?.status === "Paid" && "Paid"}
        </p>
      ),
    },
    {
      dataField: "action",
      text: "Action",
      sort: false,
      formatter: (row, data) => (
        <>
          <AiFillEye
            className="w-100"
            style={{ color: "blue", cursor: "pointer" }}
            onClick={() => viewInvoiceData(data)}
          />
        </>
      ),
      headerStyle: { backgroundColor: "#F1F1F1" },
      headerAlign: "center",
    },
  ];
  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      <b> {from} </b> to <b>{to}</b> out of <b>{size} entries</b>
    </span>
  );

  const paginateOptions = {
    paginationSize: 8,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "10",
        value: 10,
      },
    ],
  };

  useEffect(() => {
    const finalData = {
      isPagination: true,
      patientId: null,
      search: searchValue ? searchValue : null,
      createdDate: selectedDate
        ? moment(selectedDate).format("YYYY-MM-DD")
        : null,
    };
    dispatch(getPaymentDetails(finalData));
  }, [dispatch, searchValue, selectedDate]);

  return (
    <div className="doctor-patient-paymentdetails">
      <h5>Invoices</h5>
      <Card className="patient-details-card superadmin-date-wrapper">
        <Row className="pt-3 px-4">
          <Col md={3}>
            <span className="d-flex align-self-center">
              <Form.Control
                onKeyDown={(e) => {
                  if (e.key === "Enter") {
                    setSearchValue(e.target.value);
                  }
                }}
                onChange={(e) => {
                  if (e.key === "Enter") {
                    setSearchValue(e.target.value);
                  }
                }}
                type="text"
                placeholder="Search"
                className="me-2 mb-3 search-field-spacing"
                style={{ width: "100%" }}
                aria-label="Search"
              />
              <BiSearch size={22} className="searchbar-icon" />
            </span>
          </Col>

          <Col
            md={9}
            className="d-flex flex-wrap align-self-center justify-content-end"
          >
            <ReactDatePicker
              selected={selectedDate}
              onChange={(date) => setSelectedDate(date)}
              dateFormat="dd MMM yyyy"
              placeholderText="Date"
              className="custom-field-picker dr-date-w px-2"
            />
            <BsCalendarFill className="custom-date-icon" size={18} />
          </Col>
        </Row>
        <>
          <span className="doctor-datatable">
            <BootstrapTable
              columns={columns}
              data={allPaymentDetails ? allPaymentDetails : []}
              keyField="invoiceNumber"
              pagination={paginationFactory(paginateOptions)}
              // striped
              bordered={false}
              wrapperClasses="table-responsive"
            />
          </span>
        </>
      </Card>
      <Modal
        show={show}
        onHide={handleClose}
        size="xl"
        centered
        className="modal-main"
        backdrop="static"
      >
        <Modal.Header
          className="border border-0 Payment-Details-MainClass"
          closeButton
        />
        <Modal.Body className="pt-0">
          <div className="w-100 d-flex justify-content-between align-items-center">
            <p
              className="mb-0  Invoice-Number fw-bold"
              style={{ color: "#999999" }}
            >
              Invoice {viewInvoice?.invoiceNumber}
            </p>
            <button
              className="download-button-class ps-4 pe-4 rounded"
              onClick={handlePrintClick}
            >
              <BsDownload className="me-2" />
              Download
            </button>
          </div>
          <div className="w-100 d-flex justify-content-between align-items-center">
            <div className="mt-3">
              <img
                src={PaymentImage}
                className="img-fluid object-fit-cover position-absolute"
                alt="Payment Invoice"
              />
              <span className="fw-bold Profile-popup-positioning">
                Dr. {viewInvoice?.doctorName}
              </span>
              <div className="Popup-date" style={{ color: "#999999" }}>
                {moment(viewInvoice?.createdDate).format("DD MMM YYYY")}
              </div>
            </div>
            <p
              className="download-button-class px-4 rounded"
              style={{
                color: "#44BC19",
                backgroundColor: "#ECFFCC",
                border: "none",
              }}
            >
              {viewInvoice?.status}
            </p>
          </div>
          <hr style={{ color: "#c8cfd5" }} />
          <Row>
            <Col lg={6}>
              <div className="mt-3">
                <label
                  htmlFor=""
                  className="labeling-Fontsize"
                  style={{ color: "#B3B3B3" }}
                >
                  From:
                </label>
                <h5 className="fs-6 fw-bold">{viewInvoice?.patientName}</h5>
                <p className="Address-FontSize fw-bold mb-2">
                  The Business Centre
                  <br />
                  61 Wellfield Road Roath Cardiff
                </p>
                <small>ralphedwards@gmail.com</small>
                <p className="Address-FontSize mt-2">(225) 555-0118</p>
              </div>
            </Col>
            <Col lg={6} className="Bill-To-Class">
              <div className="mt-3">
                <label htmlFor="" className="labeling-Fontsize">
                  Bill to:
                </label>
                <h5 className="fs-6 fw-bold">Family Doc 24/7.</h5>
                <p className="Address-FontSize fw-bold mb-2">
                  Fitzwilliam Hall, Fitzwilliam Place, Dublin 2
                </p>
                <small>sales@familydoc247.ie</small>
                <p className="Address-FontSize mt-2">+353 12630959</p>
              </div>
            </Col>
          </Row>
          <hr style={{ color: "#c8cfd5" }} />
          <div>
            <h5 className="fs-6 fw-bold mt-4">Description</h5>
            <div
              className="d-flex justify-content-between mt-4 Description-Bar"
              style={{ backgroundColor: "#F1F1F1", padding: "5px" }}
            >
              <h6 className="ps-3 mb-2" style={{ color: "#999999" }}>
                Item
              </h6>
              <h6 className="pe-5 mb-2" style={{ color: "#999999" }}>
                Fee
              </h6>
            </div>
          </div>
          <div className="fs-6 fw-bold d-flex justify-content-between mt-4">
            <h6
              style={{ color: "#1A1A1A" }}
              className="fw-bold Description-Bar px-2"
            >
              Appointment for skin treatment
            </h6>
            <h6 className="fw-bold me-5">
              {viewInvoice?.amount ? `$${viewInvoice?.amount}` : "N/A"}
            </h6>
          </div>
        </Modal.Body>
      </Modal>
      <PrintComponents
        trigger={
          <button
            ref={printButtonRef}
            className="border-0 px-3 py-2 rounded-2 d-none"
          >
            <BsDownload size={20} /> Download PDF
          </button>
        }
      >
        <InvoicePDF invoiceData={viewInvoice} />
      </PrintComponents>
    </div>
  );
}
